<script>
import { mapState } from "vuex";
import { mapActions } from "vuex";

export default {
  name: "HeaderTicket",
  data() {
    return {
      list: false,
      isArrow: false,
      isBtnActive: false,
      isLogo: false,

      link: "/",
      activeLang: "УКР",
      menuIsVisible: false,
    };
  },
  props: {
    handleOpen: {
      type: Function,
      required: false,
    },
    name: {
      type: String,
    },
  },
  methods: {
    ...mapActions(["AUTH_LOGOUT"]),
    LOGOUT() {
      this.$store.dispatch("AUTH_LOGOUT").then(() => {
        this.$router.push("./");
      });
    },
    isBtn() {
      this.list = !this.list;
      this.isArrow = !this.isArrow;
      this.isLogo = !this.isLogo;
    },
    langChange(e) {
      this.activeLang = e.target.innerText;
    },
    login(){
      this.handleOpen()
    },
    showMenu(){
      this.menuIsVisible = !this.menuIsVisible
    }
  },
  computed: {
    ...mapState({
      token: (state) => state.token,
      isAuth: (state) => state.isAuth,
    }),
    ticketsLink() {
      return this.$route.name === "Tickets" ? "/ticket-bus" : "/";
    },
  },
};
</script>

